* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.App {
  width: 100%;
  max-width: 500px;
  padding: 0 10px;
  margin-left: auto;
  margin-right: auto;
  color: #333333;
  font-size: 16px;
  line-height: 1;
}

h1 {
  text-align: center;
  margin: 100px auto 30px auto;
}

input[type="text"] {
  font-size: 16px;
  color: inherit;
  font-family: inherit;
}

.todo__form {
  margin-bottom: 30px;
}

.todo__form input {
  width: 100%;
  padding: 10px;
}

.todo__list {
  list-style: none;
  padding: 0;
}

.todo__list--done {
  list-style: none;
  padding: 0;
  margin-top: 30px;
}

.todo__list--done .todo__item {
  margin: 4px 0;
}

.todo__item {
  display: flex;
  align-items: center;
  margin: 5px 0;
}

.todo__item span {
  flex: 1;
  white-space: nowrap;
  overflow-x: scroll;
  overflow-y: hidden;
  margin-right: 10px;
}

.todo__item span::-webkit-scrollbar {
  display: none;
}

.todo__item input[type="checkbox"] {
  width: 20px;
  height: 20px;
  margin: 0 10px 0 0;
}

.todo__item input[type="text"] {
  flex: 1;
  padding: 2px 0;
  margin-right: 10px;
  border: 0;
  border-bottom: 1px solid rgb(221, 206, 235);
}

.action {
  margin-left: auto;
}

.action button:first-child {
  margin-right: 5px;
}

button {
  padding: 5px 10px;
}

input,
button {
  border: 1px solid #cfcfcf;
  border-radius: 0;
  outline: none;
}
